<template>
  <!--  -->
  <div class="transfer">
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <!-- <div class="seachArea">
          <div class="iconArear">
            <i class="el-icon-search"></i>
          </div>
          <div class="buttomArea">
            <el-button type="info" plain size="mini"></el-button>
            <el-button type="info" plain size="mini"></el-button>
          </div>
          <div class="conditionArea">
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
          </div>
        </div>-->
        <div class="seachDataArea" v-loading.lock="loadingFlag">
          <div class="dataTitle">
            <div class="dataIconArear">
              <i class="el-icon-document"></i>
            </div>
          </div>
          <div class="dataLsitArea">
            <el-table :data="tableData" border style="width: 100%">
              <el-table-column prop="staff_id" label=""></el-table-column>
              <el-table-column label="" width="100">
                <template slot-scope="scope">
                  <div
                    class="listAvatar"
                    :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + scope.row.avatar + '.jpg)'}"
                  ></div>
                </template>
              </el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="from" label=""></el-table-column>
              <el-table-column prop="to" label=""></el-table-column>
              <el-table-column prop="time" label=""></el-table-column>
              <el-table-column prop="reason" label=""></el-table-column>
              <!-- <el-table-column label="">
                <template slot-scope="scope" style="oveflow:hidden">
                  <div class="buttomDelete" @click="handleDeleteDdata(scope.$index, scope.row)"></div>
                </template>
              </el-table-column>-->
            </el-table>
          </div>
          <div class="bottomArea">
            <el-pagination
              background
              layout="prev, pager, next, total, jumper"
              :total="tableData.length"
              @current-change="handleCurrentChange"
            ></el-pagination>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="">
        <!-- <div class="seachArea">
          <div class="iconArear">
            <i class="el-icon-search"></i>
          </div>
          <div class="buttomArea">
            <el-button type="info" plain size="mini"></el-button>
            <el-button type="info" plain size="mini"></el-button>
          </div>
          <div class="conditionArea">
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
            <div class="seachItem">
              
              <el-input v-model="input" placeholder="" size="mini"></el-input>
            </div>
          </div>
        </div>-->
        <div class="seachDataArea" v-loading="false">
          <div class="dataTitle">
            <div class="dataIconArear">
              <i class="el-icon-document"></i>
            </div>
          </div>
          <div class="dataLsitArea">
            <el-table :data="tableData1" border style="width: 100%">
              <el-table-column prop="id" label=""></el-table-column>
              <el-table-column label="" width="100">
                <template slot-scope="scope">
                  <div
                    class="listAvatar"
                    :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + scope.row.avatar + '.jpg)'}"
                  ></div>
                </template>
              </el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="sex" label=""></el-table-column>
              <el-table-column prop="department" label=""></el-table-column>
              <el-table-column prop="position" label=""></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope" style="oveflow:hidden">
                  <div class="buttomDetail" @click="handleDetail(scope.$index, scope.row)"></div>
                  <div
                    class="buttomDelete"
                    @click="handleDelete(scope.$index, scope.row)"
                    v-show="identity"
                  ></div>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="bottomArea">
            <el-pagination
              background
              layout="prev, pager, next, total, jumper"
              :total="5"
              @current-change="handleCurrentChange"
            ></el-pagination>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-dialog :title="personalInfo.name + ''" :visible.sync="dialogTableVisible" width="80%">
      <table width="100%" class="infoTable" v-loading.lock="detailLoading">
        <tr>
          <td class="personAvatar" rowspan="2">
            <div
              class="listAvatar"
              :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + personalInfo.people_id + '.jpg)'}"
            ></div>
          </td>
          <td>{{ personalInfo.people_id }}</td>
          <td> {{ personalInfo.name }}</td>
          <td>{{ personalInfo.sex }}</td>
          <td>
            
            <el-input v-model="personalInfo.department" placeholder="" :disabled="true"></el-input>
          </td>
          <td>
            
            <el-input v-model="personalInfo.position" placeholder="" :disabled="true"></el-input>
          </td>
        </tr>
        <tr>
          <td>{{ personalInfoHide.birth }}</td>
          <td colspan="2">{{ personalInfoHide.tel }}</td>
          <td>{{ personalInfoHide.degree }}</td>
          <td colspan="2">{{ personalInfoHide.register_date }}</td>
        </tr>
        <tr>
          <td colspan="2" style="height: 7vh;">{{ personalInfoHide.email }}</td>
          <td colspan="5">{{ personalInfoHide.remark }}</td>
        </tr>
      </table>
    </el-dialog>
    <el-dialog
      :title="translInfo.name + ''"
      :visible.sync="transferVisble"
      v-loading="loadingTransMsg"
      element-loading-text=""
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <table width="100%" class="infoTable">
        <tr>
          <td class="personAvatar" rowspan="2">
            <div
              class="listAvatar"
              :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + translInfo.people_id + '.jpg)'}"
            ></div>
          </td>
          <td>{{ translInfo.id }}</td>
          <td> {{ translInfo.name }}</td>
          <td>{{ translInfo.department }}</td>
          <td>{{ translInfo.position }}</td>
        </tr>
      </table>
      <div class="transMsg">*</div>
      <div class="buttomArea1">
        <div class="transBlock">
          <span class="demonstration"></span>
          <el-cascader v-model="value" :options="options"></el-cascader>
        </div>
        <div class="transBlock">
          <span class="demonstration"></span>
          <el-input v-model="dataTime" placeholder="2019-07-20"></el-input>
        </div>
        <div class="transBlock">
          <span class="demonstration" style="oveflow:left;"></span>
          <el-input type="textarea" :rows="2" placeholder="" v-model="textarea"></el-input>
        </div>
        <div class="buttomOk" @click="handleTransfer(translInfo.id)"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "transfer",
  components: {},
  data() {
    return {
      input: "",
      currpage: 1,
      dialogTableVisible: false,
      transferVisble: false,
      detailLoading: true,
      // 
      tableData: [],
      personalInfo: "",
      translInfo: "",
      loadingFlag: true,
      loadingTransMsg: false,
      // 
      tableData1: [],
      // 
      value: [],
      personalInfoHide: {},
      textarea: "",
      dataTime: "",
      options: [
        {
          label: "",
          value: ""
        },
        {
          label: "",
          value: ""
        },
        {
          label: "",
          value: ""
        }
      ],
      identity: ""
    };
  },
  methods: {
    handleDelete(index, row) {
      console.log(index, row);
      this.translInfo = row;
      this.transferVisble = true;
    },
    handleDetail(index, row) {
      var people_id = row.people_id;
      this.personalInfo = row;
      this.dialogTableVisible = true;
      this.$axios({
        method: "post",
        url: "people/list",
        data: { people_id: people_id },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.personalInfoHide = resData.data[0];
          this.detailLoading = false;
        } else {
          this.$message({
            showClose: true,
            message: "",
            type: "error"
          });
        }
      });
    },
    // 
    handleCurrentChange(cpage) {
      this.currpage = cpage;
    },
    handleDeleteDdata(index, row) {
      console.log(index, row);
    },
    // 
    getPosition_log() {
      this.$axios({
        method: "post",
        url: "/position_log/list",
        data: { staff_id: "" },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.tableData = resData.data;
          this.loadingFlag = false;
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "") {
            this.$router.push({ path: "/" });
          }
        }
      });
    },
    getAllUserMsg() {
      this.$axios({
        method: "post",
        url: "/staff/list",
        data: { staff_id: "" },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.tableData1 = resData.data;
          this.loadingFlag = false;
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "") {
            this.$router.push({ path: "/" });
          }
        }
      });
    },
    // 
    handleTransfer(id) {
      this.loadingTransMsg = true;
      var staff_id = id;
      var time = this.dataTime;
      var reason = this.textarea;
      var to = this.value[0];
      this.$axios({
        method: "post",
        url: "staff/transfer",
        data: {
          staff_id: staff_id,
          to: to,
          time: time,
          reason: reason
        },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        console.log(resData);
        if (resData.code == 0) {
          this.getAllUserMsg();
          this.getPosition_log();
          this.loadingTransMsg = false;
          this.$message({
            message: "",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "") {
            this.$router.push({ path: "/" });
          }
        }
      });
    }
  },
  mounted() {
    this.token = this.$store.getters.getUserToken;
    var identity = this.$store.getters.getIdentity;
    if (identity) {
      this.identity = true;
    } else {
      this.identity = false;
    }
    this.getPosition_log();
    this.getAllUserMsg();
  }
};
</script>

<style scoped>
.transfer {
  width: 100%;
  height: 100%;
}
.seachArea {
  width: 100%;
  height: 20vh;
  box-sizing: border-box;
