  color: #606266 !important;
}
.infoTable > tr > td > .el-input {
  margin-bottom: 0 !important;
  height: 100%;
}
.transMsg {
  width: 40%;
  margin: 2vh 0;
  font-size: 0.8vw;
  color: red;
}
.buttomArea1 {
  width: 100%;
  overflow: hidden;
}
.transBlock {
  float: left;
  width: 40%;
