}
.demonstration {
  margin-right: 1vw;
}
.buttomOk {
  float: left;
  text-align: center;
  width: 5vw;
  font-size: 1vw;
  color: #fff;
  background-color: #606266;
  font-weight: bold;
  padding: 1vh;
  border-radius: 2vw;
  margin: 1vh 2vw;
}
.buttomOk:hover {
  background-color: #fff;
  color: #606266;
  border: 1px solid #606266;
  cursor: pointer;
}
.el-input {
  width: 65%;
}
.el-textarea {
  width: 65%;
}
.demonstration {
  float: left;
  margin: 0.5vw 0;
}
</style>
